@font-face {
  font-family: 'Futura-PT';
  src: url('./font/FuturaPTLight.otf');
}

@font-face {
  font-family: 'Futura-PT-Book';
  src: url('./font/FuturaPTBook.otf');
}

@font-face {
  font-family: 'Lucida Console';
  src: url('./font/LucidaConsoleRegular.ttf');
}

body {
  position: relative;
  background-color: #DAF2FF !important;
  margin: 0;
  font-family: 'Futura-PT', 'Futura-PT-Book';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Futura-PT', 'Futura-PT-Book', 'Lucida Console';
}
